import styled from 'styled-components';
import { data } from '../data/Data';

let HeadSizeFull = `@media (min-width: 1250px) {
	height: 140px;
}`;

let HeadSizeTablet = `@media (min-width: 768px) {
	height: 180px;
}`;

let HeadSizeMobile = `height: 225px`;

let DesktopSize = `@media (min-width: 500px)`;

export const HeadLinkSide = styled.a`
	margin: 15px;
	color: ${data.styles.accent};
	text-decoration: none;
	font-size: 25px;
	&:hover {
		opacity: 0.6;
	}
`;

export const HeadFloatCon = styled.div`
	width: 100%;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	${HeadSizeTablet};
	${HeadSizeMobile};
	${HeadSizeFull};

	p {
		color: black;
		font-size: 30px;
		position: absolute;
		top: 100px;
		left: -10px;
		writing-mode: vertical-rl;
		opacity: 0.2;
		@media (min-width: 1000px) {
			display: none;
		}
	}
`;

export const HeadFlexCon = styled.div`
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	align-items: center;
	margin: auto;
	@media (min-width: 1000px) {
		justify-content: space-between;
	}
`;

export const HeadCon = styled.div`
	width: 100%; // Do take up whole screen
	position: -webkit-sticky;
	position: sticky;
	text-align: center;

	top: 0;
	right: 0;
	left: 0;
	overflow: ${(props) => props.overF};
	padding-top: 8px;
	height: 130px;
	border-bottom: ${data.styles.borderDef};
	box-sizing: border-box;
	// change the bg color based on the Component tag in the App
	background-color: ${(props) => props.colorBG};
	color: ${(props) => props.colorTX};
	// need to change the size to smaller after scroll
	opacity: ${(props) => props.opacity};
	${HeadSizeTablet};
	${HeadSizeMobile};
	${HeadSizeFull}

	.widthContainer {
		width: 70%; // don't take up whole screen
		margin: auto; // don't align to left
		overflow: hidden; // no scrollbars i f something falls out of a div
	}
`;

HeadCon.defaultProps = {
	colorBG: `${data.styles.darkBg}`,
	colorTX: `${data.styles.hover}`,
	overF: 'visible',
};

export const HeadLi = styled.li`
	display: inline;
	padding: 0 10px 0 10px;
	padding-bottom: 15px;
`;

export const FloatLi = styled.li``;

export const HeadLink = styled.a`
	//////////////////////////////////////////
	/////  Styled component with a hover style
	//////
	////////////////////////////////
	color: ${data.styles.textLight};
	cursor: pointer;
	text-decoration: none;
	text-transform: uppercase;
	font-size: 9px;
	@media (min-width: 960px) {
		font-size: 16px;
	}
	&:hover {
		color: ${data.styles.hover};
		opacity: 0.2;
	}
`;

export const CurLink = styled.span`
	color: #a55eea;
	border-bottom: 2px solid #a55eea;
	padding-bottom: 1px;
	font-size: 15px;
	text-transform: uppercase;
	cursor: pointer;
	&:hover {
		opacity: 0.2;
	}
	@media (min-width: 900px) {
		font-size: 20px;
	}
`;

export const SideCurLink = styled.span`
	color: #a55eea;
	margin: 15px;
	text-decoration: underline;
	padding-bottom: 1px;
	font-size: 12px;
	text-transform: uppercase;
	cursor: pointer;
	&:hover {
		opacity: 0.2;
	}
	@media (min-width: 900px) {
		font-size: 25px;
	}
`;

export const HeadNav = styled.nav`
	float: right;
	margin-top: 15px;
`;

export const Branding = styled.div`
	float: left;
`;

export const BrandH1 = styled.h1`
	margin: 0;
	font-size: 2em;
	padding-top: 0px;
	padding-right: 20px;
	display: block; // on mobile
	@media (min-width: 800px) {
		display: inline; // on desktop
	}
`;

export const Name = styled.h3`
	display: none;
`;

export const HeadImg = styled.img`
	cursor: pointer;
	height: 50px;
	opacity: 0.6;
	position: absolute;
	left: 25px;
	top: 30px;
	display: ${(props) => props.show};
	&:hover {
		opacity: 0.2;
	}
`;

export const HeadMenu = styled.ul`
	text-align: right;
	list-style: none;
	color: ${data.styles.accent};
	padding: 30px 15px 0 0;
	margin: auto;
	box-sizing: border-box;
	font-weight: bold;
`;

export const MainCon = styled.div`
	width: 100%;
	box-sizing: border-box;
`;
export const HeadMenuDiv = styled.div`
	text-align: left;
	padding-left: 0;
	right: 1%;
	width: 250px;
	box-sizing: border-box;
`;

export const HeadMenuLi = styled.li`
	float: left;
	font-size: 15px;
	width: 250px;
	box-sizing: border-box;
	cursor: pointer;
	&:hover {
		opacity: 0.3;
	}
	@media (min-width: 900px) {
		font-size: 22px;
	}
`;
