import styled from 'styled-components';
import { data } from '../data/Data';

export const ShowCaseCon = styled.div`
	min-height: 100px;
	width: 60%;
	margin: auto;
	background: ${data.styles.lightBg};
	color: ${data.styles.textDark};
	font-weight: bold;
	padding-bottom: 100px;
	text-align: center;
`;

export const AboutTitle = styled.div`
	padding-top: 80px;
	padding-bottom: 150px;
`;

export const ShowImg = styled.img`
	border-radius: 50%;
	float: left;
	width: 27%;
	padding-bottom: 25px;
`;

export const ShowH1 = styled.span`
	letter-spacing: 4px;
	font-size: 35px;
	font-weight: bold;
	border-bottom: ${data.styles.borderSpan};
	color: ${(props) => props.col};
	padding-bottom: 4px;
	@media (min-width: 800px) {
		font-size: 55px;
	}
`;

ShowH1.defaultProps = { color: 'white' };

export const ShowP = styled.p`
	font-size: 15px;
`;

export const ShowSubhead = styled.h2`
	font-style: italic;
`;
