import React from 'react';

import { HighlightSpan, Container, UL } from '../GlobalStyle';
import {
	HeadCon,
	HeadFlexCon,
	HeadLi,
	HeadLink,
	HeadNav,
	Branding,
	BrandH1,
	CurLink,
} from './HeadStyle';
import { sections } from '../data/Data';

class Header extends React.Component {
	constructor(props) {
		super(props);
		this.state = { active: '' };
	}

	componentDidUpdate() {}

	renderSections() {
		return sections.map((section, i) => {
			let sect = section.toString();
			if (i !== 0) {
				return (
					<HeadLi key={section}>
						<HeadLink onClick={() => this.scrollToSection(sect)}>
							{section}
						</HeadLink>
					</HeadLi>
				);
			} else {
				return (
					<HeadLink key={section}>
						<CurLink>{section}</CurLink>
					</HeadLink>
				);
			}
		});
	}

	scrollToSection = (s) => {
		let elm = document.getElementById(s);
		elm.scrollIntoView({
			behavior: 'smooth',
			block: 'start',
			inline: 'nearest',
		});
		setTimeout(() => {
			if (s === ' About') {
				window.scrollBy(0, 500);
			} else {
				window.scrollBy(0, 5);
			}
		}, 1000);
	};

	render() {
		return (
			<HeadCon>
				<div className="widthContainer">
					<HeadFlexCon id="HeadFlexCon">
						<Branding id="Branding">
							<BrandH1>
								<HighlightSpan>Rick Gove</HighlightSpan>
							</BrandH1>
							<BrandH1>Front-end Developer</BrandH1>
						</Branding>
						<HeadNav>
							<UL>{this.renderSections()}</UL>
						</HeadNav>
					</HeadFlexCon>
				</div>
			</HeadCon>
		);
	}
}

export default Header;
