import React from 'react';
import {
	FootCon,
	RightTop,
	LeftTop,
	LeftBot,
	FootContactDiv,
	FootContactImg,
	RightBot,
	FootA,
	FootIconLeft,
	BottomP,
} from './FootStyle';
import me from '../../img/RBG.jpg';
import email from '../../img/logo_email.png';
import linked from '../../img/logo_linked.png';
import facebook from '../../img/logo_facebook.png';
import twitter from '../../img/logo_twitter.png';

function Footer() {
	return (
		<FootCon id="ShouldGrid">
			<LeftTop>
				<FootIconLeft src={me} />
				<h2>
					Rick Gove <br />
					Front-end Developer <br />
				</h2>
			</LeftTop>

			<LeftBot>
				<BottomP> Copyright &copy; 2020 - Rick Gove</BottomP>
			</LeftBot>
			<RightTop>
				<FootContactDiv id="FotCOnDiv">
					<FootA href="mailto:rick.gove.developer@gmail.com">
						<FootContactImg src={email} />
					</FootA>
					<FootA href="http://www.linkedin.com/in/richard-gove-developer">
						<FootContactImg src={linked} />
					</FootA>
					{/* <FootA href="FaceBook">
						<FootContactImg src={facebook} />
					</FootA> */}
					<FootA href="https://twitter.com/gove_rick">
						<FootContactImg src={twitter} />
					</FootA>
				</FootContactDiv>
			</RightTop>
			<RightBot></RightBot>
		</FootCon>
	);
}

export default Footer;
