import styled from 'styled-components';

export const FootCon = styled.div`
	display: grid;
	grid-template-columns: 10% auto 30% 10%;
	grid-template-rows: 20% 5% auto 5% 30%;
	width: 100%;
	bottom: 0;
	overflow: hidden;
	height: 400px;
	font-size: 0.75em;
	color: black;
	padding-top: 10px;
	padding-bottom: 0px;
	background-color: white;
`;

FootCon.defaultProps = {
	colorBG: '#e8491d',
	colorTX: '#f4f4f4',
};

export const FootContactDiv = styled.div`
	margin: 0;
	display: grid;
	grid-template-columns: repeat(2, 20%);

	padding: 0;
	position: relative;
	right: 0;
	text-align: right;
	@media (min-width: 700px) {
		grid-template-columns: repeat(4, 10%);
	}
`;

export const FootContactH = styled.p``;

export const FootA = styled.a`
	margin: 0;
	padding: 0;
	&:hover {
		opacity: 0.5;
	}
`;

export const FootContactImg = styled.img`
	height: auto;
	width: 90%;
	opacity: 0.6;
`;

export const LeftTop = styled.div`
	grid-column-start: 2;
	grid-row-start: 2;
	margin-top: 0px;
	top: 50%;
	padding: 0;
	opacity: 0.6;
`;

export const LeftBot = styled.div`
	grid-column-start: 2;
	grid-row-start: 4;
	padding: 0px;
	color: darkgrey;
`;

export const RightTop = styled.h3`
	grid-column-start: 3;
	grid-row-start: 2;
`;

export const RightBot = styled.div`
	grid-column-start: 3;
	grid-row-start: 4;
`;

export const FootIcon = styled.img`
	height: 50px;
	/* float: right; */
`;

export const FootIconLeft = styled.img`
	height: 50px;
	float: left;
	padding: 10px;
	border-radius: 50%;
	opacity: 0.6;
`;

export const BottomP = styled.h2`
	float: bottom;
	padding-left: 45px;
	opacity: 0.75;
`;

export const FootLink = styled.a`
	text-decoration: none;
`;
